import React, { Component } from 'react';
import { init } from 'ityped';
import './intro.css';

export default class Intro extends Component {
	componentDidMount() {
		init(`#ele1`, {
			strings: ['', '', '', '', ''],
			typeSpeed: 180,
			backSpeed: 60,
			startDelay: 500,
			backDelay: 2500,
			loop: false,    
			showCursor: true,    
			cursorChar: "|",
			onFinished: function(){}
		});
	}
	render() {
		
		return (
			<div className="intro">
				<div className="textCover">
					<span id="ele1" className="aniText"></span>
				</div>
			</div>
		)
	}
}


